"use strict";

var util = require('util');

var hashAlgo = require('./hash-algo');

var isObject = function isObject(value) {
  if (value == null) {
    return false;
  }

  if (Array.isArray(value)) {
    return true;
  }

  if (typeof value !== 'object') {
    return false;
  }

  if (value.toString() !== '[object Object]') {
    return false;
  }

  return true;
};

var valueToString = function valueToString(value, set) {
  return Object.prototype.toString.call(value) + (value == null ? util.inspect(value) : value.toString());
};

var objectToString = function objectToString(obj, set) {
  var ary = [];

  for (var key in obj) {
    ary.push((set && +key >= 0 ? '' : key) + toString(obj[key], set));
  }

  return (set ? '' : Object.prototype.toString.call(obj)) + ary.sort();
};

var toString = function toString(obj, set) {
  return isObject(obj) ? '@' + objectToString(obj, set) : valueToString(obj, set);
};

var mask = function mask(str) {
  var hash = '';

  for (var i = 0; i < str.length; i++) {
    hash += str.charCodeAt(i).toString(16);
  }

  return hash;
};

var hash = function hash(obj, algo, set) {
  var custom = typeof algo == 'function';
  algo = algo == null || typeof algo == 'string' ? hashAlgo(algo) : algo;

  if (typeof algo != 'function') {
    set = algo;
    algo = hashAlgo();
  }

  if (typeof set != 'boolean') {
    set = false;
  }

  var str = toString(obj, set);
  return algo(custom ? mask(str) : str);
};

module.exports = hash;